<template>
<div>
  <el-form :model="reqBody" label-position='left'>
    <el-row>
      <el-col :span="8">
        <el-form-item label="用户昵称" property="userName">
          <el-input v-model="userBody.userName">  </el-input>
        </el-form-item>
      </el-col>
      <el-col :span="8">
        <el-form-item label="用户密码" property="userPassword">
          <el-input v-model="userBody.userPassword"></el-input>
        </el-form-item>
      </el-col>
      <el-col :span="8">
        <el-form-item label="用户角色" property="userRole">
          <el-select v-model="userBody.userRole" placeholder="请选择">
            <el-option
                v-for="(v,index) in this.$store.state.user.roles"
                :key="v"
                :label="v"
                :value="index">
            </el-option>
          </el-select>
        </el-form-item>
      </el-col>
    </el-row>

  </el-form>
  <el-button type="success" v-if="!this.update" @click="addUser">提交</el-button>
  <el-button type="primary" v-else @click="updateUser">保存</el-button>
  <el-button stype="danger" @click="cancel">取消</el-button>
</div>
</template>

<script>

import {userAddRequestBody} from "@/core/request/model/user/UserAddRequestBody";
import {userApi} from "@/core/request/api/UserApi";
// import {FileApi} from "@/core/api/FileApi";
// import {globalValue} from "@/config/globalconfig";
import SafeUtils from "@/core/utils/SafeUtils";
import Base64Util from "@/core/utils/Base64Util";
import CloneUtil from "@/core/utils/CloneUtil";
import {mapActions} from "vuex";

export default {
  name: "userMessageBox",
  data(){
    return{
      update:this.$parent.$parent.$parent.select!=null,
      userBody:this.$parent.$parent.$parent.select||new userAddRequestBody(),
      reqBody:null
    }
  },
  methods:{
    ...mapActions(['getUUIDputState']),
    async updateUser() {
      await this.getUUIDputState().then(res=>{
        console.log(res)
      })
      if (this.userBody.userPassword == null) delete this.userBody.userPassword
      this.reqBody = CloneUtil.clone(this.userBody)
      this.reqBody.userGender=this.userBody.userGender
      if (this.userBody.userPassword != null) this.reqBody.userPassword = SafeUtils.AesEncrypt(this.userBody.userPassword, this.$store.state.user.uuid)
      // console.log(this.reqBody);
      await this.axios.post(userApi.userUpdateAdmin, this.reqBody)
          .then(res => {
            if (!res.data.code) {
              this.$message.success(res.data.message)
            }
          })

    },
    cancel(){
      if(this.update){
        this.$parent.$parent.$parent.select=null
        this.$parent.$parent.$parent.edit=false
      }
      else this.$parent.$parent.$parent.addUser=false
    },
    uploadAvatar(param){
      var file = param.file;
      Base64Util.convertFileToBase64(file,res=>{
        this.userBody.userAvatar=res
      })
      // var data=new FormData();
      // data.append("image",file)
      // this.axios.post(FileApi.img2base64File,data)
      //     .then(res=>{
      //       console.log(res.data)
      //       if(!res.data.code){
      //         this.userBody.userAvatar=globalValue.BASE64HEADER+res.data.data
      //       }
      //       else{
      //         console.log(res.data.message)
      //       }
      //     })
      //     .catch(err=>{
      //       console.log(err.data)
      //     })
    },
    beforeAvatarUpload(file) {
      const isJPG = (file.type === 'image/jpeg') || (file.type === 'image/png') || (file.type === 'image/bmp');
      const isLt2M = file.size / 1024 / 1024 < 2;
      if (!isJPG) {
        this.$message.error('上传头像只能是 jpg、png、bmp 格式!');
      }
      if (!isLt2M) {
        this.$message.error('上传头像图片大小不能超过 2MB!');
      }
      // this.$refs.upload.submit();
      return isJPG && isLt2M;
    },
    async addUser() {
      await this.getUUIDputState().then(res => {
        console.log(res)
      })
      if (this.userBody.userPassword == null) delete this.userBody.userPassword
      this.reqBody = CloneUtil.clone(this.userBody)
      this.reqBody.userGender=this.userBody.userGender
      if (this.userBody.userPassword != null) this.reqBody.userPassword = SafeUtils.AesEncrypt(this.userBody.userPassword, this.$store.state.user.uuid)
      // console.log(this.reqBody);
      await this.axios.post(userApi.userAdd, this.reqBody)
          .then(res => {
            if (!res.data.code) {

              this.$message.success(res.data.message)
            }
          })

    }
  },
  mounted() {
    this.userBody.userPassword=null
  }
}
</script>

