<template>
  <div>
    <div class="chart-box">
      <el-row :gutter="45">
        <el-col :span="6" :offset="3">
          <el-card class="charBox">
            <div class="count1"><span>{{ CountOne }}</span></div>
            <div class="a">用户数量</div>
          </el-card>
        </el-col>
        <el-col :span="6">
          <el-card class="charBox">
            <div class="count2"><span>{{ CountTwo }}</span></div>
            <div class="b">
              接管API数量
            </div>
          </el-card>
        </el-col>
        <el-col :span="6">
          <el-card class="charBox">
            <div class="count3"><span :key="new Date().getTime()">{{ getRunnedTime }}</span></div>
            <div class="b">
              运行天数
            </div>
          </el-card>
        </el-col>
      </el-row>
      <el-row>
        <el-col :span="12">
          <el-card id="charThree">
          </el-card>
        </el-col>
        <el-col :span="12">
          <el-card id="charFour">
          </el-card>
        </el-col>
      </el-row>
    </div>
  </div>
</template>

<script>
import * as echarts from 'echarts';
import {CountApi} from "@/core/request/api/CountApi";

export default {
  name: "dataPage",
  data() {
    return {
      CountOne: '',
      CountTwo: '',
      countRunning: null,
      columns: [],
      values: [],
      userIncColumns: [],
      userIncValues: []
    }
  },
  mounted() {
    this.getincreaseCount(),
        this.getGraphIncDataWithUser(),
        this.getUserCount(),
        this.getHomeCount()
    this.getRunningTime()
  },
  computed: {
    getRunnedTime() {
      var old_date = new Date(this.countRunning); //设置过去的一个时间点，"yyyy-MM-dd HH:mm:ss"格式化日期
      // eslint-disable-next-line no-constant-condition
      var new_date = new Date(); //新建一个日期对象，默认现在的时间
      var difftime = (new_date - old_date) / 1000; //计算时间差,并把毫秒转换成秒
      var days = parseInt(difftime / 86400); // 天  24*60*60*1000
      var hours = parseInt(difftime / 3600) - 24 * days;    // 小时 60*60 总小时数-过去的小时数=现在的小时数
      var minutes = parseInt(difftime % 3600 / 60); // 分钟 -(day*24) 以60秒为一整份 取余 剩下秒数 秒数/60 就是分钟数
      var seconds = parseInt(difftime % 60);  // 以60秒为一整份 取余 剩下秒数
      return (days + "天" + hours + "小时" + minutes + "分钟" + seconds + "秒");

    }
  },
  methods: {

    getUserCount() {
      this.axios.get(CountApi.userCount).then(res => {
        if (res.data.code !== 0) return this.$message.error('获取用户数量发生错误')
        this.CountOne = res.data.data
        // console.log(res)
      })
    },
    getRunningTime() {
      this.axios.get(CountApi.runTime).then(res => {
        if (res.data.code !== 0) return this.$message.error('获取运行时间发生错误')
        this.countRunning = res.data.data
        // console.log(res)
      })
    },
    getHomeCount() {
      this.axios.get(CountApi.docCount).then(res => {
        if (res.data.code !== 0) return this.$message.error('获取用户数量发生错误')
        this.CountTwo = res.data.data
      })
    },
    getincreaseCount() {
      this.axios.get(CountApi.increaseCount, {
        params: {
          day: 7,
        }
      }).then(res => {
        if (res.data.code !== 0) return this.$message.error('获取数据发生错误')
        this.columns = res.data.data.columns
        this.values = res.data.data.values
        this.setCharOne()
        // console.log(res)
      })
    },
    getGraphIncDataWithUser() {
      this.axios.get(CountApi.userIncCount, {
        params: {
          day: 7,
        }
      }).then(res => {
        if (res.data.code !== 0) return this.$message.error('获取数据发生错误')
        this.userIncColumns = res.data.data.columns
        this.userIncValues = res.data.data.values
        this.setCharTwo()
      })
    },
    setCharOne() {
      // 基于准备好的dom，初始化echarts实例
      const charThree = echarts.init(document.getElementById('charThree'));
      const option = {
        title: {
          text: '智库日增量',
          left: 'center',
          top: '5px',
        },
        toolbox: {
          // 工具栏
          show: true, // 要显示
          feature: {
            // 各工具配置项
            mark: {show: true}, // 辅助线开关
            dataView: {show: true, readOnly: false}, // 数据视图工具，可以展现当前图表所用的数据，编辑后可以动态更新。
            magicType: {show: true, type: ['bar']}, // 动态类型切换
            restore: {show: true}, // 配置项还原
            saveAsImage: {show: true} // 点击保存下载图片
          }
        },
        tooltip: {
          trigger: 'axis',
          axisPointer: {
            type: 'cross',
            label: {
              backgroundColor: '#6a7985',
            }
          },
        },
        xAxis: {
          name: '时间',
          type: 'category',
          data: this.columns
        },
        yAxis: {
          name: '增加数量',
          type: 'value'
        },
        series: [
          {
            name: '增加数量',
            data: this.values,
            type: 'line'
          }
        ]
      };
      charThree.setOption(option)
    },
    setCharTwo() {
      // 基于准备好的dom，初始化echarts实例
      const charFour = echarts.init(document.getElementById('charFour'));
      const option = {
        title: {
          text: '用户日增量',
          left: 'center',
          top: '5px',
        },
        toolbox: {
          // 工具栏
          show: true, // 要显示
          feature: {
            // 各工具配置项
            mark: {show: true}, // 辅助线开关
            dataView: {show: true, readOnly: false}, // 数据视图工具，可以展现当前图表所用的数据，编辑后可以动态更新。
            magicType: {show: true, type: ['bar']}, // 动态类型切换
            restore: {show: true}, // 配置项还原
            saveAsImage: {show: true} // 点击保存下载图片
          }
        },
        tooltip: {
          trigger: 'axis',
          axisPointer: {
            type: 'cross',
            label: {
              backgroundColor: '#6a7985',
            }
          },
        },
        xAxis: {
          name: '时间',
          type: 'category',
          data: this.userIncColumns
        },
        yAxis: {
          name: '增加数量',
          type: 'value'
        },
        series: [
          {
            name: '增加数量',
            data: this.userIncValues,
            type: 'line'
          }
        ]
      };
      charFour.setOption(option)
    }
  }
}
</script>

<style scoped>
.el-row {
  margin-bottom: 20px;
}

.count1 {
  position: relative;
  font-style: normal;
  font-size: 70px;
  font-family: 'Arial';
  color: #83a2ed;
  line-height: 120px;
  text-align: center;
}

.count2 {
  font-style: normal;
  font-size: 70px;
  font-family: 'Arial';
  color: #83a2ed;
  line-height: 120px;
  text-align: center;
}

.count3 {
  font-style: normal;
  font-size: 25px;
  font-family: 'Arial';
  color: #83a2ed;
  line-height: 120px;
  text-align: center;
}

.charBox .a {
  font-style: normal;
  font-size: 29px;
  font-family: 'Arial';
  background-color: #ffffff;
  color: #83a2ed;
  bottom: 0px;
  text-align: center;
}

.charBox .b {
  font-style: normal;
  font-size: 29px;
  font-family: 'Arial';
  background-color: #ffffff;
  color: deepskyblue;
  bottom: 0px;
  text-align: center;
}

#charThree {
  width: 550px;
  height: 250px;
  margin: 35px auto;
  border-radius: 20px;
  border: 1px solid #83a2ed;
}

#charFour {
  width: 550px;
  height: 250px;
  margin: 35px auto;
  border-radius: 20px;
  border: 1px solid #83a2ed;
}
</style>
