import {globalValue} from "@/core/config/globalConfig";

export class userAddRequestBody{
    /**
     * 用户昵称
     */
    userName;

    /**
     * 账号
     */
    userAccount;
    /**
     * 用户简介
     */
    userProfile;
    /**
     * 用户头像
     */
    userAvatar=globalValue.BASE64HEADER+"";

    /**
     * 性别
     */
    gender;

    /**
     * 用户角色: user, admin
     */
    userRole;

    /**
     * 密码
     */
    userPassword;
    userGender;
    checkPassword;
}